@import url("https://fonts.googleapis.com/css2?family=Changa+One&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');


.hero-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;

}

.hero-container h1 {
  color: white;
  font-size: 90px;
  margin-top: -300px;
  text-shadow: 2px 4px 4px #aaa;
  font-family: 'Dancing Script', cursive;
  transition: all 2.5s;
  
}

.hero-container h1 {
  transform: scale(1.7);

}
.hero-container>p {
  margin-top: 8px;
  color: #fff;
  font-size: 42px;
  font-family: 'Dancing Script', cursive;
  
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container h1 {
    font-size: 70px;
    margin-top: -150px;

  }
}

@media screen and (max-width: 768px) {
  .hero-container h1 {
    font-size: 50px;
    margin-top: -100px;
  }
  .hero-container>p {
    font-size: 30px;
  }
  .btn-mobile {
    display: block;
    text-decoration: none;
  }
  .btn {
    width: 100%;
  }
}